<template>
    <v-container class="container invoice">
        <v-card v-if="invoice">
            <!-- Actions  -->
            <v-toolbar
                dark
                color="grey darken-3"
            >
                <div class="invoice-chip">
                    <p>Status: </p>
                    <v-chip
                        label
                        class="mt-2 ml-3"
                        :class="{ paid: invoice.invoicePaid, draft: invoice.invoiceDraft, pending: invoice.invoicePending }"
                    >
                        <v-icon left class="ml-3">mdi-circle-medium</v-icon>
                        <span v-if="invoice.invoicePaid">Paid</span>
                        <span v-if="invoice.invoiceDraft">Draft</span>
                        <span v-if="invoice.invoicePending">Pending</span>
                    </v-chip>
                </div>
                <v-spacer></v-spacer>
                <v-btn-toggle>
                    <v-btn
                        @click="updateStatusInvoice"
                    >
                        <v-icon left>mdi-note-edit-outline</v-icon>
                        Draft
                    </v-btn>
                    <v-btn
                        color="red"
                        @click="deleteInvoice(invoice.invoiceId)"
                    >
                        <v-icon left>mdi-delete-forever-outline</v-icon>
                        Delete
                    </v-btn>
                    <v-btn
                        color="light-blue"
                        @click="toggleEditInvoice"
                    >
                        <v-icon left>mdi-pencil</v-icon>
                        Edit
                    </v-btn>
                </v-btn-toggle>
            </v-toolbar>

            <!-- Biller/Client  -->
            <v-card-title
                class="details ma-5"
            >
                <!-- Biller  -->
                <v-layout row wrap class="py-5 px-3 mb-3" id="biller">
                    <v-flex xs12 md8>
                        <h4 class=" display-1 font-weight-bold" id="iId"><span>#</span>{{ invoice.invoiceId }}</h4>
                        <p>{{ invoice.productDescription }}</p>
                    </v-flex>
                    <v-spacer></v-spacer>
                    <v-flex xs12 md3 class="text-end">
                        <p>{{ invoice.billerStreetAddress }}</p>
                        <p>{{ invoice.billerCity }}</p>
                        <p>{{ invoice.billerZipCode }}</p>
                        <p>{{ invoice.billerCountry }}</p>
                    </v-flex>
                </v-layout>

                <!-- Client  -->
                <v-row class="client my-5 pt-5">
                    <v-col cols="12" sm="6" md="3">
                        <p class="text-subtitle-2">Invoice Date:</p>
                        <p>{{ invoice.invoiceDate }}</p>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                        <p class="text-subtitle-2">Client:</p>
                        <p>{{ invoice.clientName }}</p>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                        <p class="text-subtitle-2">Email:</p>
                        <p>{{ invoice.clientEmail }}</p>
                    </v-col>
                    <v-col cols="12" sm="6" md="3">
                        <p class="text-subtitle-2">Payment Date:</p>
                        <p>{{ invoice.paymentDueDate }}</p>
                    </v-col>
                    <v-col cols="9" md="3">
                        <p>{{ invoice.clientStreetAddress }}</p>
                        <p>{{ invoice.clientCity }}</p>
                        <p>{{ invoice.clientZipCode }}</p>
                        <p>{{ invoice.clientCountry }}</p>
                    </v-col>
                    <v-spacer></v-spacer>
                </v-row>

            </v-card-title>

            <!-- Invoice items  -->
            <v-card-text>
                <v-data-table
                    darken
                    :headers="tableHeader"
                    :items="invoice.invoiceItemList"
                    class="elevation-1 mx-4"
                    hide-default-footer
                ></v-data-table>
            </v-card-text>

            <!-- Total  -->
            <v-card-actions>
                <div class="ma-5 row total">
                    <p>Amount Due:</p>
                    <v-spacer></v-spacer>
                    <p class="display-1"><span class="mr-1">$</span> {{ invoice.invoiceTotal }}</p>
                </div>
            </v-card-actions>
        </v-card>
        <Skeleton v-else/>
        <SwitchDialog v-if="invoice" />
        <Dialog v-if="invoice"/>
    </v-container>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import Skeleton from '@/components/Skeleton/invoiceView_skeleton'
import SwitchDialog from '@/components/Dialogs/switch_invoice_dialog'
import Dialog from '@/components/Dialogs/invoice_dialog'

export default {
    name: "invoiceView",
    components: { Skeleton, SwitchDialog, Dialog },
    data(){
        return {
            tableHeader: [
                {text: 'Name', value: 'name'},
                {text: 'Quantities', value: 'qty'},
                {text: 'Price ($)', value: 'price'},
                {text: 'Total ($)', value: 'total'}
            ]
        }
    },
    created(){
        this.getInvoiceInUse(this.$route.params.invoiceId);
    },
    methods: {
        ...mapMutations(['SET_LOADED_INVOICE','UPDATE_SWITCH_DIALOG','UPDATE_DIALOG','TOGGLE_EDIT_INVOICE']),
        ...mapActions(['getInvoiceInUse']),

        toggleEditInvoice(){
            // this.$store.commit('UPDATE_DIALOG', !this.dialog);
            this.UPDATE_DIALOG();
            this.TOGGLE_EDIT_INVOICE();
        },

        deleteInvoice(targetId){
            alert('Delete this invoice, ' + targetId)
        },

        updateStatusInvoice(){
            this.UPDATE_SWITCH_DIALOG();
        }
    },
    computed: {
        ...mapState({
            invoice:        state => state.invoices.loadedInvoice,
            switchDialog:   state => state.invoices.switchDialog,
            dialog:         state => state.invoices.dialog,
        }),
    }
}
</script>

