<template>
   <v-dialog
        :value="dialog"
        persistent
        max-width="600px"
    >
        <ValidationObserver v-slot="{ submitForm }">
            <v-card>
                <!-- <DToolbar :name="this.name" :mountedClick="$store.dispatch('switchDialog', !$store.state.invoices.dialog)"/> -->
                <v-toolbar
                    dark
                    color="indigo"
                >
                    <v-toolbar-title v-if="editInvoice">Invoice #{{ invoiceId }}</v-toolbar-title>
                    <v-toolbar-title v-else>Create New Invoice.</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn
                        icon
                        dark
                        @click="closeInvoiceDialog"
                    >
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-toolbar>
                <v-card-text>
                    <v-container>
                        <form @submit.prevent="submitForm(onSubmit)" class="invoice-content">
                            <!-- Bill From  -->
                            <v-card-title class="pb-0">Bill From:</v-card-title>
                            <v-card-text>
                                <ValidationProvider name="Biller's Street Address" rules="required" v-slot="{ errors }">
                                    <v-text-field  label="Street Address" v-model="billerStreetAddress" :error-messages="errors" required></v-text-field>
                                </ValidationProvider>
                                <v-row>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Biller's City" rules="required" v-slot="{ errors }">
                                            <v-select :items="selectedStateList" label="City" v-model="billerCity" :error-messages="errors" required></v-select>
                                        </ValidationProvider>
                                    </v-col>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Biller's Postcode" rules="required|min:4|max:4" v-slot="{ errors }">
                                            <v-text-field  label="Post Code" v-model="billerZipCode" :error-messages="errors" required></v-text-field>
                                        </ValidationProvider>
                                    </v-col>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Biller's Country" rules="required" v-slot="{ errors }">
                                            <v-select :items="selectedCountryList"  label="Country" v-model="billerCountry" :error-messages="errors" required></v-select>
                                        </ValidationProvider>
                                    </v-col>
                                </v-row>
                            </v-card-text>
                            <v-spacer></v-spacer>

                            <!-- Bill To  -->
                            <v-card-title class="pb-0">Bill To:</v-card-title>
                            <v-card-text>
                                <ValidationProvider name="Client's Name" rules="required" v-slot="{ errors }">
                                    <v-text-field  label="Client's Name" v-model="clientName" :error-messages="errors" required></v-text-field>
                                </ValidationProvider>
                                <ValidationProvider name="Client's Email" rules="required" v-slot="{ errors }">
                                    <v-text-field  label="Client's Email" v-model="clientEmail" :error-messages="errors" required></v-text-field>
                                </ValidationProvider>
                                <ValidationProvider name="Client's Street Address" rules="required" v-slot="{ errors }">
                                    <v-text-field  label="Street Address" v-model="clientStreetAddress" :error-messages="errors" required></v-text-field>
                                </ValidationProvider>
                                <v-row>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Client's City" rules="required" v-slot="{ errors }">
                                            <v-text-field  label="City" v-model="clientCity" :error-messages="errors" required></v-text-field>
                                        </ValidationProvider>
                                    </v-col>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Client's Postcode" rules="required|min:4|max:4" v-slot="{ errors }">
                                            <v-text-field  label="Post Code" v-model="clientZipCode" :error-messages="errors" required></v-text-field>
                                        </ValidationProvider>
                                    </v-col>
                                    <v-col  md="4" sm="4">
                                        <ValidationProvider name="Client's Country" rules="required" v-slot="{ errors }">
                                            <v-select :items="selectedCountryList"  label="Country" v-model="clientCountry" :error-messages="errors" required></v-select>
                                        </ValidationProvider>
                                    </v-col>
                                </v-row>
                            </v-card-text>

                            <!-- Invoice Work Details  -->
                            <v-card-text>
                                <v-row>
                                    <v-col md="5" sm="6">
                                        <v-text-field  label="Invoice Date" v-model="invoiceDate" disabled></v-text-field>
                                    </v-col>
                                    <v-col md="5" offset-md="2" sm="6">
                                        <v-text-field  label="Payment Date" v-model="paymentDueDate" disabled></v-text-field>
                                    </v-col>
                                </v-row>
                                <ValidationProvider name="Payment Terms" rules="required" v-slot="{ errors }">
                                    <v-select :items="listTerms" label="Payment Terms" v-model="paymentTerms" :error-messages="errors"></v-select>
                                </ValidationProvider>
                                <v-text-field  label="Product's Description" v-model="productDescription"></v-text-field>
                            </v-card-text>
                            <v-card-title>Item List</v-card-title>
                            <v-card-text>
                                <v-simple-table dense>
                                    <template v-slot:default>
                                        <thead>
                                            <tr>
                                                <th
                                                    v-for="(item, index) in tableHead"
                                                    :key=index
                                                >
                                                    {{ item.text }}
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr
                                                v-for="(item, index) in invoiceItemList"
                                                :key="index"
                                            >
                                                <td><v-text-field v-model="item.name"></v-text-field></td>
                                                <td>
                                                    <v-text-field v-model="item.qty" type="number" @change="updateTotal"></v-text-field>
                                                </td>
                                                <td><v-text-field v-model="item.price" prepend-icon="mdi-currency-usd" readonly></v-text-field></td>
                                                <td>
                                                    <v-text-field :value="(item.total = item.qty * item.price).toFixed(2)" readonly>
                                                        <template v-slot:prepend>
                                                            <v-icon>mdi-currency-usd</v-icon>
                                                        </template>
                                                        <template v-slot:append-outer>
                                                            <v-icon color="red lighten-2" @click="deleteInvoiceItem(item.id)">mdi-delete-forever</v-icon>
                                                        </template>
                                                    </v-text-field>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </template>
                                </v-simple-table>
                                <div id="addNewItem" @click="addNewInvoiceItem" class="py-1 text-center text-uppercase text--secondary text-button">
                                    <v-icon color="light-blue lighten-3">mdi-plus</v-icon>
                                    Add New Item
                                </div>
                            </v-card-text>

                            <!-- Save/Exit Actions  -->
                            <v-card-actions class="mt-5">
                                <v-btn
                                    class="ma-2"
                                    color="error"
                                    outlined
                                    @click="closeInvoice"
                                >Discard</v-btn>
                                <v-spacer></v-spacer>
                                <v-btn
                                    class="ma-2"
                                    outlined
                                    color="grey"
                                    v-if="!editInvoice"
                                    @click="saveDraft"
                                >Save Draft</v-btn>
                                <v-btn
                                    class="ma-2"
                                    color="primary"
                                    type="submit"
                                    dark
                                    v-if="!editInvoice"
                                    @click="uploadInvoice"
                                >Create Invoice</v-btn>
                                <v-btn
                                    class="ma-2"
                                    color="primary"
                                    type="submit"
                                    dark
                                    v-if="editInvoice"
                                    @click="uploadInvoice"
                                >Update Invoice</v-btn>
                            </v-card-actions>
                        </form>
                    </v-container>
                </v-card-text>
            </v-card>
        </ValidationObserver>
    </v-dialog>
</template>

<script>
import { uid } from 'uid';
import { mapState, mapMutations } from 'vuex';
import db from '../../firebase/firebaseinit';

export default {
    name: "invoiceDialog",
    data() {
        return {
            billerStreetAddress:    null,
            billerCity:             null,
            billerZipCode:          null,
            billerCountry:          null,
            clientName:             null,
            clientEmail:            null,
            clientStreetAddress:    null,
            clientCity:             null,
            clientZipCode:          null,
            clientCountry:          null,
            invoiceDateUnix:        null,
            invoiceDate:            null,
            paymentTerms:           null,
            paymentDueDateUnix:     null,
            paymentDueDate:         null,
            productDescription:     null,
            invoicePending:         null,
            invoiceDraft:           null,
            invoiceTotal:           0,

            // Stable list
            invoiceItemList:        [{name: 'abdc', qty: 12, price: 5.9, total: 12}],
            selectedStateList:      [],
            selectedCountryList:    [],
            dateOptions:            { year: "numeric", month: "short", day: "numeric"},
            listTerms:              [
                                        { text: 'Next 30 days', value: '30' },
                                        { text: 'Next 45 days', value: '45' },
                                        { text: 'Next 60 days', value: '60' },
                                        { text: 'Next 75 days', value: '75' },
                                        { text: 'Next 90 days', value: '90' }
                                    ],
            tableHead:              [   { text: 'Item Name',    width: '60%'},
                                        { text: 'Qty',          width: '70px'},
                                        { text: 'Price',        width: '50px'},
                                        { text: 'Total',        width: '20%'}
                                    ],

        };
    },
    created() {
        // get current date for invoice date field
        if( this.editInvoice ) {
            this.invoiceDateUnix = Date.now();
            this.invoiceDate = new Date(this.invoiceDateUnix).toLocaleDateString("en-us", this.dateOptions)
        }
        if ( !this.editInvoice ) {
            this.invoiceId=              this.loadedInvoice.invoiceId,
            this.billerStreetAddress=    this.loadedInvoice.billerStreetAddress,
            this.billerCity=             this.loadedInvoice.billerCity,
            this.billerZipCode=          this.loadedInvoice.billerZipCode,
            this.billerCountry=          this.loadedInvoice.billerCountry,
            this.clientName=             this.loadedInvoice.clientName,
            this.clientEmail=            this.loadedInvoice.clientEmail,
            this.clientStreetAddress=    this.loadedInvoice.clientStreetAddress,
            this.clientCity=             this.loadedInvoice.clientCity,
            this.clientZipCode=          this.loadedInvoice.clientZipCode,
            this.clientCountry=          this.loadedInvoice.clientCountry,
            this.invoiceDateUnix=        this.loadedInvoice.invoiceDateUnix,
            this.invoiceDate=            this.loadedInvoice.invoiceDate,
            this.paymentTerms=           this.loadedInvoice.paymentTerms,
            this.paymentDueDateUnix=     this.loadedInvoice.paymentDueDateUnix,
            this.paymentDueDate=         this.loadedInvoice.paymentDueDate,
            this.productDescription=     this.loadedInvoice.productDescription,
            this.invoiceItemList=        this.loadedInvoice.invoiceItemList,
            this.invoiceTotal=           this.loadedInvoice.invoiceTotal,
            this.invoicePending=         this.loadedInvoice.invoicePending,
            this.invoiceDraft=           this.loadedInvoice.invoiceDraft,
            this.invoicePaid=            this.loadedInvoice.invoicePaid
        }
    },
    mounted() {

        fetch('https://restcountries.com/v2/all?fields=name')
            .then(res   => res.json())
            .then(data  => data.forEach(item => this.selectedCountryList.push(item.name)))
            .catch(err => console.log(err.message)),
        fetch('https://countriesnow.space/api/v0.1/countries/cities',{
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body:JSON.stringify({
                    "country": "Australia"
                })
            })
            .then(res   => res.json())
            .then(result  => { result.data.forEach(item => this.selectedStateList.push(item));})
            .catch(err  => console.log(err.message))
    },
    methods: {
        ...mapMutations(['UPDATE_DIALOG','SET_LOADED_INVOICE','TOGGLE_EDIT_INVOICE']),

        closeInvoiceDialog(){
            this.UPDATE_DIALOG();
            if(this.editInvoice){
                this.TOGGLE_EDIT_INVOICE();
            }
        },
        deleteInvoiceItem(id) {
            this.invoiceItemList = this.invoiceItemList.filter(item => item.id !== id)
        },
        addNewInvoiceItem() {
            this.invoiceItemList.push({
                id:         uid(),
                name:       '',
                qty:        0,
                price:      0,
                total:      0,
            })
        },
        updateTotal(event) {
            this.item.qty = event.target.value;
            this.item.total = this.item.qty * this.item.price
        },
        calculateInvoiceTotal() {
            this.invoiceTotal = 0;
            this.invoiceItemList.forEach(item => {
                this.invoiceTotal += item.total;
            });
        },
        closeInvoice(){
            this.$router.push('/invoice');
        },
        saveDraft(){
            this.invoiceDraft = true;
        },
        async uploadInvoice(){
            if (this.invoiceItemList.length <= 0) {
                alert('Please ensure you filled out work items !');
                return;
            }
            this.loading = true;
            this.calculateInvoiceTotal();

            const database = db.collection('invoices').doc()

            await database.set({
                invoiceId:              uid(6),
                billerStreetAddress:    this.billerStreetAddress,
                billerCity:             this.billerCity,
                billerZipCode:          this.billerZipCode,
                billerCountry:          this.billerCountry,
                clientName:             this.clientName,
                clientEmail:            this.clientEmail,
                clientStreetAddress:    this.clientStreetAddress,
                clientCity:             this.clientCity,
                clientZipCode:          this.clientZipCode,
                clientCountry:          this.clientCountry,
                invoiceDateUnix:        this.invoiceDateUnix,
                invoiceDate:            this.invoiceDate,
                paymentTerms:           this.paymentTerms,
                paymentDueDateUnix:     this.paymentDueDateUnix,
                paymentDueDate:         this.paymentDueDate,
                productDescription:     this.productDescription,
                invoiceItemList:        this.invoiceItemList,
                invoiceTotal:           this.invoiceTotal,
                invoicePending:         this.invoicePending,
                invoiceDraft:           this.invoiceDraft,
                invoicePaid:            null
            })

        },
        submitForm(){
            // this.uploadInvoice();
            console.log("Form is submitted.")
        }
    },
    computed: {
        ...mapState({
            dialog:         state => state.invoices.dialog,
            loadedInvoice:  state => state.invoices.loadedInvoice,
            editInvoice:    state => state.invoices.editInvoice
        })
    },
    watch: {
        paymentTerms() {
            const futureDate = new Date();
            this.paymentDueDateUnix = futureDate.setDate(futureDate.getDate() + parseInt(this.paymentTerms));
            this.paymentDueDate = new Date(this.paymentDueDateUnix).toLocaleDateString("en-us", this.dateOptions)
        }
    }
}
</script>
<style >
#addNewItem{
    border: 2px dashed indigo;
    cursor: pointer;
}
#addNewItem:hover{
    background-color: #EEE;
}
</style>