<template>
    <v-dialog
        :value="switchDialog"
        persistent
        max-width="400px"
    >
        <v-card>
            <v-toolbar
                dark
                color="indigo"
            >
                <v-toolbar-title>Switch Invoice Status.</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn
                    icon
                    dark
                    @click="handleClick"
                >
                    <v-icon>mdi-close</v-icon>
                </v-btn>
            </v-toolbar>
            <v-card-text>
                <v-row>
                    <v-col
                        cols="6"
                        sm="4"
                    >
                        <v-switch
                            v-model="invoice.invoiceDraft"
                            :value="invoice.invoiceDraft"
                            label="Draft"
                            color="cyan"
                            hide-details
                        ></v-switch>
                    </v-col>
                    <v-col
                        cols="6"
                        sm="4"
                    >
                        <v-switch
                            v-model="invoice.invoicePending"
                            :value="invoice.invoicePending"
                            label="Pending"
                            color="orange"
                            hide-details
                        ></v-switch>
                    </v-col>
                    <v-col
                        cols="6"
                        sm="4"
                    >
                        <v-switch
                            v-model="invoice.invoicePaid"
                            :value="invoice.invoicePaid"
                            label="Paid"
                            color="teal"
                            hide-details
                        ></v-switch>
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
    methods: {
        ...mapMutations(['UPDATE_SWITCH_DIALOG','SET_LOADED_INVOICE']),
        handleClick(){
            this.$store.commit('UPDATE_SWITCH_DIALOG', !this.switchDialog);
        }
    },
    computed: {
        ...mapState({
            switchDialog:   state => state.invoices.switchDialog,
            invoice:        state => state.invoices.loadedInvoice,
        })
    }
}
</script>

